import fetch from 'node-fetch'
import { SearchParametersType } from '../types'
import { ZOHO } from '../vendor/ZSDK'

type AddressGeocodeType = {
    searchAddress: string | null
    apiKey: string
}

type RecordType = {
    id: string
    Phone: string | null
    Full_Name: string
    Street: string
    City: string
    State: string
    Zip_Code: string
    Country: string
    Latitude: string
    Longitude: string
}

export async function findMatchingProperties ({ searchAddress }: SearchParametersType) {
    const response = await ZOHO.CRM.FUNCTIONS.execute('find_nearby_contacts', {
        arguments: JSON.stringify({
            search_address: searchAddress,
            radius_km: 999.0
        })
    })

    return response
}

export async function getGoogleMapsApiKeyFromCRM () {
    await ZOHO.embeddedApp.init()
    const googleMapsAPIKey = await ZOHO.CRM.API.getOrgVariable('ethicaltechnology_google_maps_api_key')
    if (Object.keys(googleMapsAPIKey).includes('Error')) {
        alert(`Issue with google maps API organisation variable: ${googleMapsAPIKey.Error.Content}. Make sure you've added the key.`)
    }
    return googleMapsAPIKey.Success.Content
}

export async function getRecords (page: number) {
    await ZOHO.embeddedApp.init()
    const response = await ZOHO.CRM.API.getAllRecords({
        Entity: 'Mold_Test_Inspector',
        page,
        per_page: 200
    })
    const data = response.data.map((item: RecordType) => {
        return {
            address: `${item.Street}, ${item.City}, ${item.State}, ${item.Zip_Code}, ${item.Country}`,
            position: {
                latitude: parseFloat(item.Latitude),
                longitude: parseFloat(item.Longitude)
            },
            id: item.id,
            phoneNumber: item.Phone,
            name: item.Full_Name
        }
    })
    return data
}

export async function getAddressGeocode ({ searchAddress, apiKey }: AddressGeocodeType) {
    const url = `https://maps.googleapis.com/maps/api/geocode/json?address=${searchAddress}&key=${apiKey}`
    const res = await fetch(url)
    const body = await res.json()
    if (body.results.length > 0) {
        const coordinates = {
            latitude: body.results[0].geometry.location.lat,
            longitude: body.results[0].geometry.location.lng
        }
        return {
            coordinates,
            status: 'Success'
        }
    }
    return {
        msg: 'Unable to fetch coordinates.',
        status: 'Failed'
    }
}
