import React, { useEffect, useState } from 'react'
import { compact, sortBy } from 'lodash'
import { DistanceMatrixService, LoadScript, GoogleMap, Marker } from '@react-google-maps/api'
import { AddressType, PositionType, ResultItemType, ResultsType } from '../types'

type MapProps = {
    results: ResultsType
    mapsApiKey: string
    setResultsSortedByDistance: (T: ResultItemType[]) => void
    resultsSortedByDistance: AddressType[] | null
}

type MatrixType = {
    status: string
    duration: {
        value: number
        text: string
    }
    distance: {
        value: number
        text: string
    }
}

type DistanceMatrixType = {
    origin_addresses: string[]
    destination_addresses: string[]
    rows: {
        elements: MatrixType[]
    }[]
}

export function MapWidget ({ results, mapsApiKey, setResultsSortedByDistance, resultsSortedByDistance }: MapProps) {
    const [centrePoint, setCentrePoint] = useState<PositionType>({
        latitude: 27.9767,
        longitude: -81.6489
    })
    const [destinations, setDestinations] = useState<string[] | null>(null)

    useEffect(() => {
        if (results.addressesToRender) {
            const destinationsAddresses = results.addressesToRender.map((address) => address.address)
            setDestinations(destinationsAddresses)
        }
    }, [results.addressesToRender])

    useEffect(() => {
        if (centrePoint) {
            setCentrePoint(centrePoint)
        }
    }, [results.centrePoint])

    const distanceMatrixCallback = (response: DistanceMatrixType) => {
        if (destinations) {
            const distance = response.rows[0].elements.map((item: MatrixType, index: number) => {
                if (item.status === 'OK' && results.addressesToRender) {
                    const data = {
                        ...results.addressesToRender[index],
                        distance: item.distance,
                        duration: item.duration
                    }
                    return data
                }
                return null
            })
            const resultsSortedByDistance = sortBy(compact(distance), ['distance.value'])
            setResultsSortedByDistance(resultsSortedByDistance)
            setDestinations(null)
        }
    }

    return (
        <div style={{ padding: '10px 50px' }}>
            <LoadScript googleMapsApiKey={mapsApiKey}>
                <GoogleMap
                    center={{
                        lat: centrePoint.latitude,
                        lng: centrePoint.longitude
                    }}
                    zoom={11}
                    mapContainerStyle={{
                        width: 'auto',
                        height: '500px'
                    }}
                >
                    { results.centrePoint && destinations && (
                        <DistanceMatrixService
                            options={{
                                destinations,
                                origins: [{ lng: results.centrePoint.longitude, lat: results.centrePoint.latitude }],
                                travelMode: 'DRIVING'
                            }}
                            callback={distanceMatrixCallback}
                        />
                    )}
                    { results.centrePoint && (
                        <Marker
                            position={{
                                lat: results.centrePoint.latitude,
                                lng: results.centrePoint.longitude
                            }}
                            label={'0'}
                        >
                            { resultsSortedByDistance && resultsSortedByDistance.map((item, index) => {
                                const label = index + 1
                                return (
                                    <Marker
                                        key={item.address}
                                        position={{
                                            lat: item.position.latitude,
                                            lng: item.position.longitude
                                        }}
                                        label={label.toString()} />
                                )
                            })}
                        </Marker>
                    )}
                </GoogleMap>
            </LoadScript>
        </div>
    )
}
