import React, { useState } from 'react'
import { ResultItemType } from '../types'

const pageSize = 20

type TableResultsType = {
    results: ResultItemType[]
}

export function TableResults ({ results }: TableResultsType) {
    const [page, setPage] = useState(1)

    function paginate (array: ResultItemType[], page_size: number, page_number: number) {
        return array.slice((page_number - 1) * page_size, page_number * page_size)
    }

    if (results.length > 0) {
        return (
            <div style={{ padding: '10px 50px' }}>
                <table>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Name</th>
                            <th>Address</th>
                            <th>Phone Number</th>
                            <th>Distance</th>
                            <th>Duration</th>
                        </tr>
                    </thead>
                    <tbody>
                        { paginate(results, pageSize, page).map((item: ResultItemType, index: number) => {
                            if (item.id) {
                                return (
                                    <tr key={`item.name-${item.id}`}>
                                        <td>{(pageSize * (page - 1)) + (index + 1)}</td>
                                        <td>{item.name}</td>
                                        <td>{item.address}</td>
                                        <td>{item.phoneNumber}</td>
                                        <td>{item.distance.text}</td>
                                        <td>{item.duration.text}</td>
                                    </tr>
                                )
                            }
                            return null
                        }) }
                    </tbody>
                </table>
                <div style={{ marginBottom: 50 }}>
                    { page > 1 && (
                        <button style={{ marginRight: 10 }} onClick={() => setPage(page - 1)}>
                            Previous
                        </button>
                    )}
                    { !(page * pageSize >= results.length) && (
                        <button onClick={() => setPage(page + 1)}>
                            Next
                        </button>
                    ) }

                </div>
            </div>

        )
    }
    return null
}
